import * as t from "io-ts";
import { PostAssetsV, PostAssetsWithoutDependenciesV } from "@/types/assets";

export const SsrNodeV = t.intersection([
  t.type({
    html: t.string
  }),
  PostAssetsWithoutDependenciesV
]);

// this type also exists in Psalm
export type SsrNode = t.TypeOf<typeof SsrNodeV>;

export const SsrNodeWithDependenciesV = t.intersection([SsrNodeV, PostAssetsV]);

// this type also exists in Psalm
export type SsrNodeWithDependencies = t.TypeOf<typeof SsrNodeWithDependenciesV>;
