















































































































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { Breakpoint } from "@/types/breakpoints";
import { configStore } from "@/store/store";
import {
  mediaQueryString,
  validateBreakpoint
} from "@/lib/breakpoints/breakpoints";
import defensiveCopy from "@/util/defensive-copy";
import { pipe } from "fp-ts/lib/function";
import { fold } from "fp-ts/lib/Either";
import NotificationLogger from "@/lib/notifications/notifications";
import IconButton from "@/components/UI/IconButton.vue";
import TextInput from "@/components/Reusable/CustomInputs/TextInput.vue";
import DropdownInput from "@/components/Reusable/CustomInputs/DropdownInput.vue";
import NiceButton from "@/components/UI/NiceButton.vue";
import ConfirmDialog from "@/components/UI/ConfirmDialog.vue";

@Component({
  components: {
    IconButton,
    TextInput,
    DropdownInput,
    NiceButton,
    ConfirmDialog
  },
  directives: {
    focus: {
      // focus element on mount
      inserted: function(el) {
        el.focus();
      }
    }
  }
})
export default class EditBreakpoint extends Vue {
  @Prop()
  value!: Breakpoint;
  @Prop()
  isEditing!: boolean;

  deleteDialog = false;

  // values can be undefined, which is not reactive in Vue.
  defaultPreviewWidth: number | undefined | string | null = String(
    this.value.defaultPreviewWidth || ""
  );
  minWidth: number | undefined | string | null = String(
    this.value.minWidth || ""
  );
  maxWidth: number | undefined | string | null = String(
    this.value.maxWidth || ""
  );

  setDefaultPreviewWidth(width: "100%" | "custom") {
    this.defaultPreviewWidth = width === "100%" ? "100%" : "";
  }

  enableEditing() {
    this.$emit("edit", this.value.id);
  }

  get breakpoints() {
    return configStore.breakpoints;
  }

  get previewWidthUnit() {
    return "px";
  }

  deleteBreakpoint() {
    this.$emit("delete");
    this.deleteDialog = false;
  }

  get mediaQueryString() {
    const minWidth = parseInt(String(this.minWidth));
    const maxWidth = parseInt(String(this.maxWidth));

    return mediaQueryString(minWidth, maxWidth);
  }

  cancel() {
    this.minWidth = this.value.minWidth;
    this.maxWidth = this.value.maxWidth;
    this.defaultPreviewWidth = this.value.defaultPreviewWidth;
    this.$emit("edit", "");
  }

  save() {
    const newBreakpoint = defensiveCopy(this.value);

    newBreakpoint.minWidth = parseInt(String(this.minWidth)) || undefined;
    newBreakpoint.maxWidth = parseInt(String(this.maxWidth)) || undefined;
    newBreakpoint.defaultPreviewWidth =
      this.defaultPreviewWidth === "100%"
        ? "100%"
        : parseInt(String(this.defaultPreviewWidth));

    this.minWidth = newBreakpoint.minWidth ? newBreakpoint.minWidth : "";
    this.maxWidth = newBreakpoint.maxWidth ? newBreakpoint.maxWidth : "";
    this.defaultPreviewWidth = newBreakpoint.defaultPreviewWidth
      ? newBreakpoint.defaultPreviewWidth
      : "";

    pipe(
      validateBreakpoint(newBreakpoint),
      fold(
        errors => {
          errors.forEach(e => NotificationLogger.log.message(e.msg));
        },
        breakpoint => {
          this.$emit("input", breakpoint);
          this.$emit("edit", "");
        }
      )
    );
  }
}
