






































import { configStore } from "@/store/store";
import Vue from "vue";
import { Component } from "vue-property-decorator";
import TopBarSection from "@/components/Reusable/AppLayout/TopBar/TopBarSection.vue";

@Component({
  components: {
    TopBarSection
  }
})
export default class UpgradeToPro extends Vue {
  dialogOpen = true;

  toggleDialog() {
    this.dialogOpen = !this.dialogOpen;
  }

  get isTrialExpired() {
    return configStore.trialData && configStore.trialData.isExpired;
  }
}
