
























import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { BreakpointOptions } from "@/types/controls";
import defensiveCopy from "@/util/defensive-copy";

@Component({
  components: {}
})
export default class ElementStudioBreakpointOptions extends Vue {
  @Prop()
  value!: BreakpointOptions;

  updateMultiple(multiple: boolean) {
    const newBreakpointOptions = defensiveCopy(this.value);
    newBreakpointOptions.multiple = multiple;
    this.emit(newBreakpointOptions);
  }

  updateEnableNever(enableNever: boolean) {
    const newBreakpointOptions = defensiveCopy(this.value);
    newBreakpointOptions.enableNever = enableNever;
    this.emit(newBreakpointOptions);
  }

  emit(breakpointOptions: BreakpointOptions | undefined) {
    this.$emit("input", breakpointOptions);
  }
}
