



























import Vue from "vue";
import { Component } from "vue-property-decorator";
import { uiStore, documentStore, configStore } from "@/store/store";
import IconButton from "@/components/UI/IconButton.vue";
import Dropdown from "@/components/Reusable/Dropdown/Dropdown.vue";
import ElementBadge from "./ElementBadge.vue";

@Component({
  components: {
    Dropdown,
    IconButton,
    ElementBadge
  }
})
export default class Titlebar extends Vue {
  searchEnabled = false;

  get elementTitle() {
    return uiStore.activeElement
      ? configStore.element(uiStore.activeElement.data.type).name
      : "No Active Element"; /* should never happen */
  }
  get elementBadge() {
    return uiStore.activeElement
      ? configStore.element(uiStore.activeElement.data.type).badge
      : false;
  }
  duplicateElement() {
    if (uiStore.activeElementId) {
      documentStore.duplicateElementAndActivateIt(uiStore.activeElementId);
    }
  }
  remove() {
    if (uiStore.activeElementId !== null) {
      documentStore.deleteElement({
        nodeId: uiStore.activeElementId
      });
    }
  }
}
